@import url(https://fonts.googleapis.com/css?family=Montserrat);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  margin-top: 6vh;
}

html {
  background-color: black;
  font-family: montserrat, Verdana, Geneva, Tahoma, sans-serif;
}

.donate {
  position: fixed;
  bottom: 0;
  right: 0;
}

.github-link {
  position: fixed;
  bottom: 5px;
  left: 5px;
  color: gray;
}

.form-holder {
  width: 60%;
  margin: auto;
}

@media only screen and (max-width: 900px) {
  .form-holder {
    width: 90%;
  }
}
.wuphf-form-input {
  width: 90%;
  padding: 8px 12px;
  margin: 8px 3px;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  font-weight: bold;
}

#website {
  display: none;
}

.wuphf-form-textarea {
  width: 90%;
  height: 10vh;
  padding: 8px 12px;
  margin: 8px 3px;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  font-weight: bold;
}

.wuphf-form {
  border-radius: 5px;
  background-color: #f2f2f2;
  padding: 20px;
  margin: auto;
  align-content: left;
}

.wuphf-form-input-div {
  margin-bottom: 3px;
}

.wuphf-submit-button {
  width: 80%;
  background-color: #524a93;
  font-weight: bolder;
  color: white;
  padding: 14px 20px;
  margin: 8px 0;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1.2rem;
}

.loading-spinner {
  display: block;
  position: fixed;
  z-index: 1031;
  top: calc(50% - 100px / 2); /* where ... is the element's height */
  right: calc(50% - 100px / 2); /* where ... is the element's width */
}

.logo-image {
  width: auto;
  max-width: 100%;
  max-height: 20vh;
  margin-bottom: 3%;
}

::-webkit-input-placeholder {
  /* Chrome/Opera/Safari */
  color: black;
  font-weight: 100;
}
::-moz-placeholder {
  /* Firefox 19+ */
  color: black;
  font-weight: 100;
}
:-ms-input-placeholder {
  /* IE 10+ */
  color: black;
  font-weight: 100;
}
:-moz-placeholder {
  /* Firefox 18- */
  color: black;
  font-weight: 100;
}

.wuphf-error {
  color: red;
  font-size: 0.8rem;
  margin-top: 8px;
}

